<template>
    <div class="fadeInDown" v-show="isScrolled" :class="isScrolled ? 'fixed-top' : ''">
        <img height="50px" src="/img/mobile-logo-print.png" alt="rafikibet-logo" @click="$router.push({name: 'home'})">
        <TopnavTabs></TopnavTabs>
    </div>
</template>

<script>
    import TopnavTabs from "./TopnavTabs";
    export default {
        name: "FixedScrollNavBar",
        components: {TopnavTabs},
        data () {
            return {
                isScrolled: false
            }
        },
        created () {
            window.addEventListener('scroll', this.handleScroll)
        },
        methods: {
            handleScroll () {
                this.isScrolled = window.pageYOffset >= 95;

            },
            destroyed () {
                window.removeEventListener('scroll', this.handleScroll);
            }
        }
    }
</script>

<style scoped>
    .fixed-top{
        overflow: hidden;
        background: #142c37 !important;
        position: fixed; /* Set the navbar to fixed position */
        top: 0; /* Position the navbar at the top of the page */
        width: 100%; /* Full width */
    }


</style>
